/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Button } from "@mui/material";

import CurateMain from "../assets/curate_mobile.svg";
import CurateMainMd from "../assets/curate.svg";
import AnalyzeImg from "../assets/analyse.svg";
import AdvancedImg from "../assets/advanced.svg";
import ScheduleImg from "../assets/schedule.svg";

const Curate = ({ isMobile, className }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="intro">
          <div className="data">
            <div className="description">
              <span className="badge">{"CURATE"}</span>
              <h2 className="title">{"Auto-Magic Social Media Management."}</h2>
              {isMobile && (
                <div className="image">
                  <img
                    src={CurateMain}
                    alt="Feature-Engage"
                    className="feature-image"
                    height={327}
                    width={379}
                  />
                </div>
              )}
              <p className="info">
                {
                  "Hassle free social media management is here. Organize, Plan and Automate all your social media platforms task in one go with Curate by EasySocial."
                }
              </p>
              <Button variant="contained" className="action">
                {"Coming Soon"}
              </Button>
            </div>
          </div>
          {!isMobile && (
            <div className="image">
              <img
                src={CurateMainMd}
                alt="Feature-Engage"
                className="feature-image"
                height={539}
                width={498}
              />
            </div>
          )}
        </div>
        <div className="features">
          <div className="block">
            <div className="block-image">
              <img src={ScheduleImg} alt="Plan & Schedule" />
            </div>
            <div className="info">
              <h3>{"Plan & Schedule"}</h3>
              <p>{"Easy calendar interface for planning social media."}</p>
              <p>{"Schedule Images, Videos, GIFs and more."}</p>
            </div>
          </div>
          <div className="block">
            <div className="block-image">
              <img
                src={AnalyzeImg}
                alt="Analyze & Measure"
                className="block-image"
              />
            </div>
            <div className="info">
              <h3>{"Analyze & Measure"}</h3>
              <p>{"Analyze post performance across platforms in one click"}</p>
              <p>{"Download performance reports."}</p>
            </div>
          </div>
          <div className="block">
            <div className="block-image">
              <img
                src={AdvancedImg}
                alt="Advanced Features"
                className="block-image"
              />
            </div>
            <div className="info">
              <h3>{"Advanced Features"}</h3>
              <p>{"Tag locations, people & pages."}</p>
              <p>{"View & respond to comments across all platforms."}</p>
            </div>
          </div>
        </div>
        <div className="cta">
          <div className="header">
            <h4>
              {
                "Imagine the convenience of managing all social media in one place."
              }
            </h4>
          </div>
          <div className="actions">
            {false && (
              <Button variant="outlined" className="explore">
                {"Explore"}
              </Button>
            )}
            <Button variant="contained" className="demo">
              {"Coming Soon"}
            </Button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Curate;
